import Vue from 'vue';
import Router from 'vue-router';
Vue.use(Router);

export default new Router({
  base: process.env.BASE_URL,
  routes: [{
      path: '/',
      name: 'home',
      meta: {
        state: "Ready to start?!"
      },
      component: require("./views/trickster-cup/Home.vue").default
    },
    {
      path: '/play',
      name: 'play',
      meta: {
        state: "Playing..."
      },
      component: require("./views/trickster-cup/Play.vue").default
    },
    {
      path: '/choose/:cup_id',
      name: 'result',
      props: true,
      meta: {
        state: "And the result is..."
      },
      component: require("./views/trickster-cup/Result.vue").default
    },
  ]
});