<template>
  <div id="app">
    <nav class="level">
      <div class="level-item has-text-centered">
        <button
          v-model="playAgain"
          @click="togglePlayAgain"
          to="/play"
          class="button link"
          v-if="state === 'Playing...'"
        >
          <span>Play again!</span>
        </button>
      </div>
      <div class="level-item has-text-centered">
        <router-link to="/">
          <h1>Trickster and the Cup</h1>
        </router-link>
        <h2>{{ state }}</h2>
      </div>
      <div class="level-item ghost"></div>
    </nav>
    <main class="trickster">
      <transition name="page" mode="out-in">
        <router-view class="view"/>
      </transition>
    </main>
  </div>
</template>

