<script>
export default {
  data() {
    return {
      state: this.$router.currentRoute.meta.state,
      playAgain: false,
      correctCup: ""
    };
  },
  mounted() {
    console.log(this.state);
  },
  methods: {
    togglePlayAgain() {
      this.playAgain = "play";
      if (this.$router.currentRoute.name === "play") {
        axios.get("/play").then(response => console.log(response.data));
      } else {
        this.$router.replace("/play");
      }
    }
  },
  watch: {
    $route(to, from, next) {
      this.state = to.meta.state;
    }
  }
};
</script>



