<template>
  <div class="trickster--graphics">
    <button
      v-model="buttons[idx]"
      @click="checkCup(idx)"
      v-for="(btn, idx) in buttons"
      :key="idx"
      :data-cup="idx"
      :disabled="gameOver"
    >
      <div class="cnt">
        <img width="175" src="../../assets/SVG/cupx.svg" alt>
        <span>{{ idx }}</span>
      </div>
      <div :class="'result '+isCorrectClass(idx)" v-text="buttons[idx].result"></div>
    </button>
    <div class="game-over" v-if="gameOver">
      <button @click="reset" class="button btn-again">Game is over. Play again!</button>
    </div>
  </div>
</template>
<style lang='scss'>
.trickster--graphics {
  text-align: center;
  margin-top: 150px;
  button:not(.btn-again) {
    border: none;
    background: transparent;
    display: inline-block;
    cursor: pointer;
    padding: 0;
    margin: 50px;
    position: relative;

    span {
      position: absolute;
      font-weight: 600;
      left: 50%;
      top: 50%;
      color: white;
      transform: translate(-50%, -50%);
      font-size: 1.4rem;
    }
    .result {
      position: absolute;
      left: 50%;
      bottom: -50px;
      width: 100%;
      transform: translate(-50%, -50%);
      font-size: 0.9rem;
      &.success {
        color: $primary;
      }
      &.danger {
        color: $secondary;
      }
    }
    .cnt {
      transition: transform 0.4s ease;
      img {
        transition: opacity 0.3s ease;
      }
    }
    &:focus {
      outline: none;
      // border-bottom: 3px solid $primary;
    }
    &:hover {
      .cnt {
        transform: translateY(-10px);
      }
    }
    &:disabled {
      // pointer-events: none;
      cursor: no-drop;
      &:hover .cnt {
        transform: none;
      }
      img {
        opacity: 0.4;
      }
    }
  }
  .game-over {
    display: block;
    margin-top: 40px;
  }
}
</style>
<script>
export default {
  data() {
    return {
      buttons: {
        0: { isCorrect: false, result: "" },
        1: { isCorrect: false, result: "" },
        2: { isCorrect: false, result: "" }
      },
      cupCount: 0
    };
  },
  mounted() {
    console.log("Play component mounted!");
    axios.get("/play").then(response => console.log(response));
  },
  methods: {
    checkCup(idx) {
      axios.get("/choose/"+idx).then(response => {
        console.log(response.data);
        const won = "You chose the right cup!";
        const lost = "You chose the wrong cup!";
        if (response.data === idx) {
          this.buttons[idx].result = won;
          this.buttons[idx].isCorrect = true;
          this.cupCount = 3;
        } else {
          this.buttons[idx].result = lost;
          this.buttons[idx].isCorrect = false;
          this.cupCount++;
        }
      });
      this.buttons[idx].click = true;
    },
    reset() {
      for (const btn in this.buttons) {
        this.buttons[btn].isCorrect = false;
        this.buttons[btn].result = "";
      }
      this.cupCount = 0;
      axios.get("/play").then(response => console.log(response));
    },
    isCorrectClass(idx) {
      return this.buttons[idx].isCorrect ? "success" : "danger";
    }
  },
  computed: {
    gameOver() {
      console.log(this.cupCount);
      if (this.cupCount === 3) {
        return true;
      }
      return false;
    }
  }
};
</script>